.app-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px;
  }
  
  .search-input {
    width: 100%;
    padding: 10px;
    margin-bottom: 10px;
    border: 1px solid #ccc;
    border-radius: 4px;
  }
  
  .transaction-form {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    width: 100%;
    max-width: 500px;
    margin-bottom: 20px;
  }
  
  .input-field,
  .add-button {
    flex: 1 1 auto;
    padding: 10px;
    margin: 0 5px;
    min-width: 100px;
  }
  
  .input-field {
    border: 1px solid #ccc;
    border-radius: 4px;
  }
  
  .add-button {
    padding: 10px 20px;
    background-color: #c2f1b8;
    color: rgb(0, 0, 0);
    border: none;
    border-radius: 2px;
    cursor: pointer;
    align-self: center;
  }
  
  .add-button:hover {
    background-color: hsl(115, 100%, 35%);
  }
  
  .transactions-table {
    width: 100%;
    border-collapse: collapse;
    margin-bottom: 20px;
  }
  
  .transactions-table th,
  .transactions-table td {
    padding: 10px;
    border: 1px solid #ccc;
    text-align: left;
  }
  
  .transactions-table th {
    background-color: #f2f2f2;
  }
  h1 {
    text-align: center;
    background-color: rgb(62, 16, 105);
    color: rgb(255, 255, 255);
  }
  